html {
  box-sizing: border-box;
  font-size: 62.5%;
  width: 100vw;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  width: 100vw;
}

.container {
  margin: 0 auto;
  max-width: 150rem;
  width: 90%;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

h1 {
  margin: 0;
}